<template>
  <BaseLayout title="我的订单">

    <van-pull-refresh v-model="state.refreshing" @refresh="onRefresh">
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
        <van-row class="block" align="center" v-for="(item,index) in state.list" :key="index" @click="popup(item)">
          <van-col span="24" class="dt"><van-tag color="#CCC">{{item.dt_create}}</van-tag></van-col>
          <van-col span="12" class="id"># {{item.id}}</van-col>
          <van-col span="12" class="status" :style="{color:item.color}">{{item.status}}</van-col>
          <van-col span="19" class="detail">
            <van-image class="img" v-for="(v,k) in item.detail" :key="k" :src="v.image" fit="contain"></van-image>
          </van-col>
          <van-col span="5" class="amount">
            <div>共 {{item.total}} 件</div>
            <div>￥ {{item.amount}}</div>
          </van-col>
        </van-row>
      </van-list>
    </van-pull-refresh>

    <van-action-sheet v-model:show="state.show1" title="订单详情">
      <van-collapse class="bill" v-model="choose" accordion>
        <van-collapse-item name="1" title="商品数量" :value="(state.item.card <= 0 ? state.item.total : state.item.card + ' x ' + state.item.total / state.item.card) + ' 件'">
          <van-cell v-for="(v,k) in state.item.detail" :key="k" :label="v.spec" :style="{alignItems:'center'}">
            <template #icon><van-icon class="img" :name="v.image" size="32"></van-icon></template>
            <template #title><div class="van-ellipsis">{{v.title}}</div></template>
            <template #extra>x {{v.num}}</template>
          </van-cell>
        </van-collapse-item>
        <van-collapse-item name="2" title="订单总额" :value="state.item.amount + ' 元'">
          <van-cell title="实付积分" :value="state.item.point + ' 分'"></van-cell>
          <van-cell title="实付现金" :value="state.item.cash + ' 元'"></van-cell>
        </van-collapse-item>
        <van-collapse-item name="3" title="交易状态" :value="state.item.result">
          <van-cell title="创建时间" :value="state.item.dt_create"></van-cell>
          <van-cell title="付款时间" :value="state.item.dt_pay"></van-cell>
          <van-cell title="发货时间" :value="state.item.dt_send"></van-cell>
          <van-cell title="完成时间" :value="state.item.dt_finish"></van-cell>
        </van-collapse-item>
        <van-collapse-item name="4" title="收货方式" :value="state.item.card == 0 ? '物流' : (state.item.card < 0 ? '卡密' : '提货卡')">
          <div v-if="state.item.card == 0" v-html="state.item.logistics"></div>
          <van-swipe class="swipe" v-else>
            <van-swipe-item class="sitem" v-for="(v,k) in state.cards" :key="k" :style="{background:v.bg}">
              <div v-if="state.item.card > 0">
                <div class="card">提货卡号：****{{v.token.substr(4)}}</div>
                <div class="tool">
                  <van-icon name="more-o"  @click="copy(v.token)"> 复制卡号</van-icon>
                  &nbsp;
                  <van-icon name="photo-o" @click="save(v.token)"> 显示卡片</van-icon>
                </div>
              </div>
              <div v-if="state.item.card < 0">
                <div class="card">{{v.no}}</div>
                <div class="tool">
                  <van-icon name="more-o" v-show="v.pwd && v.pwd.length > 0" @click="$copy(v.no + '\n' + v.pwd)"> 复制卡密</van-icon>
                </div>
                <div class="deadline" v-show="v.deadline && v.deadline.length > 0">
                  有效期至：{{v.deadline}}
                </div>
              </div>
            </van-swipe-item>
            <template #indicator="{ active }">
              <div class="indicator">{{ active + 1 }} / {{state.cards.length}}</div>
            </template>
          </van-swipe>
        </van-collapse-item>
      </van-collapse>
      <div class="action">
        <van-button v-show="state.item.status==0" type="primary" text="立即付款" @click="onClickButton" block round />
        <van-button v-show="state.item.card==0 && state.item.status==2" type="success" text="确认收货" @click="onClickButton" block round />
        <van-button v-show="state.item.card==0 && state.item.status==3" type="danger" text="申请售后" @click="onClickButton" block round />
      </div>
    </van-action-sheet>

    <van-popup v-model:show="state.show2"><van-image class="giftcard" :src="state.giftcard" width="300"><br/>长按图片保存到手机或分享给好友</van-image></van-popup>

  </BaseLayout>
</template>

<style scoped>
.list {
  min-height: 500px;
  padding: 5px;
  background: #F9F9F9;
  border-top: solid 1px #EEE;
}
.list .block {
  margin-top: 45px;
  padding: 0 10px 10px 10px;
  background: #FFF;
  border-radius: 10px;
  border: solid 1px #F6F6F6;
  font-size: 12px;
}

.list .dt {
  position: relative;
  top: -25px;
  text-align: center;
}
.list .id {
  font-size: 14px;
  font-weight: 600;
}
.list .status {
  text-align: right;
}
.list .detail {
  float: left;
  width: 100%;
  height: auto;
  overflow-x: scroll;
}
.list .img {
  width: 80px;
  height: 80px;
  margin: 10px 3px 0 0;
  border: solid 1px #F6F6F6;
  border-radius: 5px;
}
.list .amount {
  border-left: solid 1px #EEE;
  font-size: 12px;
  text-align: right;
}


.bill {
  min-height: 200px;
}
.bill .img {
  margin-right: 10px;
  border-radius: 5px;
}

.swipe {
  width: 100%;
  height: 100px;
  overflow: hidden;
}
.indicator {
  position: absolute;
  left: 5px;
  top: 5px;
  padding: 2px 5px;
  color: #FFF;
  background: rgba(0, 0, 0, 0.5);
  font-size: 12px;
}
.card {
  margin-top: 30px;
  color: #666;
  font-size: 18px;
  font-weight: 800;
  text-align:center;
}
.tool {
  position: absolute;
  color: #333;
  font-size: 12px;
  right: 10px;
  bottom: 10px;
}
.deadline {
  position: absolute;
  color: #333;
  font-size: 12px;
  left: 10px;
  bottom: 10px;
}

.action {
  padding: 10px;
}

.giftcard {
  margin: 10px;
  font-size: 12px;
  text-align: center;
}
</style>

<script>
import {reactive,ref} from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  setup() {
    const state = reactive({
      refreshing: false,
      loading: false,
      finished: false,
      list: [],
      offset: 0,
      limit : 20,

      show1: false,
      item: {},
      cards: [],

      show2: false,
      giftcard: "",
    });
    const choose = ref('4');
    const active = ref(0);
    return {state, choose};
  },

  mounted(){
    this.onRefresh();
  },

  computed: {
  },

  methods: {
    onRefresh() {
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
    },
    onLoad() {
      this.$http.get("/mall/orderlist?offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push({...item, ...{
            status: ["未付款", "已付款", "已发货", "交易成功", "交易关闭"][item.status],
            color: ["#CCC", "#666", "#00C", "#0C0", "#C00"][item.status],
          }});
        }
      });
    },

    popup(item) {
      this.state.cards = [];
      this.$http.post("/mall/orderDetail?id=" + item.id).then((data) => {
        this.state.show1 = true;
        this.state.item = data;
        this.state.item.result = ["未付款", "已付款", "已发货", "交易成功", "交易关闭"][data.status];
        if (item.card != 0){
          for (var i in data.cards){
            data.cards[i].bg = "rgb(" + _.random(150,250) + "," + _.random(150,250) + "," + _.random(150,250) + ")";
          }
          this.state.cards = data.cards;
        }
      });
    },

    copy(token){
      let str = "亲，送你一张提货卡略表心意！请关注微信公众号“福鲤社”，点击下方菜单提货，提货卡号：" + token;
      this.$copy(str);
    },

    save(token) {
      this.state.show2 = true;
      this.state.giftcard = this.$host + '/mall/card?type=pick&token=' + token;
    },
  }
}
</script>
